<template>
<!--订单中心-->
<div id="first">
    <div id="Content02">
        <div id="header">
            <center><h1>我的订单</h1></center>
        </div>
        <el-table  :data="filter"  stripe  style="width: 100%">
                    
                 <el-table-column label="商品名称"  prop="tShopinf.sname"  width="140">
                </el-table-column>
                <el-table-column  prop="tShopinf.sphoto"  label="商品图片"  width="140">
                        <template slot-scope= "scope">
                        <img :src="scope.row.tShopinf.sphoto" alt= "加载失败" style= "width:50px;height:50px">
                        </template>
                </el-table-column>
                <el-table-column  prop="tShopinf.sprice"  label="商品价格"  width="140">
                </el-table-column>
                <el-table-column  label="详细信息"  width="200">
                        <template slot-scope= "scope">
                        <el-button type="text" @click="find(scope.row)">查看详细信息</el-button>
                        </template>   
                </el-table-column>
                <el-table-column   label="操作" >
                    <template slot-scope= "scope">
                        <el-button type="danger" size="small" @click="del(scope.$index,scope.row)">删除</el-button> 
                        <el-button type="primary" size="small" @click="buy(scope.$index,scope.row)">购买</el-button>   
                    </template>               
            </el-table-column>
        </el-table>
          
        <el-pagination  layout="prev, pager, next"  :total="len"  :page-size="pageSize" :current-page="currentPage" @current-change="handleCurrentChange" style="text-align: center" >
        </el-pagination>  
    </div>
</div>
</template>

<script>
import axios from 'axios'
axios.defaults.headers["Content-Type"] = "application/x-www-form-urlencoded";
export default ({
   name:"Content02",
    data() 
    {
      return {
        order:[],
        currentPage: '1', //当前页 刷新后默认显示第一页
        pageSize: '5', //每一页显示的数据量 此处每页显示5条数据
        len:6,
      }
    },

    methods: 
    {
    
        //点击按钮切换页面
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化   
        },

        
        find(row) 
        {         
            
            var name1;
            var tel1;
            var credit1;
              
            //通过卖家id查询卖家信息
            //要封装成formdata类型
            var params = new URLSearchParams()
            params.append('PK_UID', row.tOrderlist.sellerUid)
            axios.post('/api/user/getOneUser',params)
            .then((response)=>{
                    console.log(response.data); //测试  
                    name1=response.data.data.user.uname;
                    tel1=response.data.data.user.uphone; 
                    credit1=response.data.data.user.ulevel;   
                     //弹出商品的基本信息
                    this.$alert("<div>卖家名字: "+name1+"</div>"+"<div>卖家电话: "+tel1+"</div>"+
                        "<div>卖家信用度: "+credit1+"</div>"+"<div>商品简介: "+row.tShopinf.sintroduction+"</div>"  
                        , '商品信息', {
                        dangerouslyUseHTMLString: true
                    });
            })          
        },

        //从购物车删除
        del(index,row)
        {
            
            this.$confirm('你是否要将本件商品从购物车中删除?', '提示', 
            {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
     
                var params = new URLSearchParams()
                params.append('O_SID', row.tOrderlist.sid)
                params.append('O_buyerUID', this.$store.state.uid)
                axios.post('/api/orderlist/deleteFromCart',params)
                .then((response)=>{
                        console.log(response.data); //测试 
                        
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    this.order.splice(index,1);      
                }) 


            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
            });          
            });       
        },

        //结算
        buy(index,row)
        {

             this.$confirm('你确定购买本商品吗?', '提示', 
            {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
     
                var params = new URLSearchParams()
                params.append('O_SID', row.tOrderlist.sid)
                params.append('O_buyerUID', this.$store.state.uid)
                axios.post('/api/orderlist/buy',params)
                .then((response)=>{
                        console.log(response.data); //测试 
                        
                        this.$message({
                            type: 'success',
                            message: '购买!'
                        });
                    this.order.splice(index,1);      
                }) 

            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消购买'
            });          
            });       

        }

    },
    computed:
    {
        //过滤用户订单只显示合法且未被支付的
         filter()
         {
              var newlist=[];                         
              this.order.forEach(element => {                            
                  if(element.tOrderlist.state==0)
                  {
                     newlist.push(element);
                  }
              });
              this.len=newlist.length;
              return newlist.slice((this.currentPage - 1) * this.pageSize,this.currentPage *this.pageSize);
         },  
    
    },
    mounted()
    {
  
       //获取订单表,未完成
        var params = new URLSearchParams()
        params.append('O_buyerUID',this.$store.state.uid)
        axios.post('/api/orderlist/getAbleOrderLists',params)
        .then((response)=>{
              console.log(response.data); //测试
              this.order=response.data.data.order;    
               
        })
  
    }
})
</script>

<style scoped>
#Content02
{
    width: 60%;
    position: absolute;
    left:20%;
}
#footer
{
    height: 45px;
    position: absolute;
    left: 70%;
    margin-top: 30px;
}
#first
{
    background-color:#e8e5e5;
    width:auto;
    height: 680px;
}
span
{
     color: #e71818;
}
.a
{
    margin-left: 30px;
    width: 100px;

}
</style>