<template>
<!--个人中心-->
<div id="first">
    <div id="Content04" align="center">
            <h1>个人信息表</h1>
            <el-descriptions class="margin-top"  :column="2" :size="size" border >
                    <el-descriptions-item >
                        <template slot="label">
                            <i class="el-icon-user"></i>姓名  
                        </template>
                    <el-input  v-model="form.name" :disabled="open"></el-input>
                    </el-descriptions-item>

                    <el-descriptions-item >
                        <template slot="label">
                            <i class="el-icon-edit"></i>学号
                        </template>
                        <el-input  v-model="form.studentNumber" disabled></el-input>
                    </el-descriptions-item>

                    <el-descriptions-item >
                        <template slot="label">
                            <i class="el-icon-mobile-phone"></i>手机号  
                        </template>
                        <el-input  v-model="form.tel" :disabled="open"></el-input>
                    </el-descriptions-item>

                    <el-descriptions-item >
                    <template slot="label">
                        <i class="el-icon-office-building"></i>
                        宿舍号
                    </template>
                    <el-input  v-model="form.room" :disabled="open"></el-input>
                    </el-descriptions-item>

                    <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-tickets"></i>
                        学校
                    </template>
                    <el-input  v-model="form.school" :disabled="open"></el-input>
                    </el-descriptions-item>

                    <el-descriptions-item>
                    <template slot="label">
                        <i class="el-icon-help"></i>
                        信用度
                    </template>
                    <el-input  v-model="form.credit" disabled></el-input>
                    </el-descriptions-item>

                </el-descriptions>
                <el-button type="danger" round @click="open=false" class="a">修改</el-button>
                <el-button type="warning" round @click="close()" class="b">确认</el-button>
    </div>
</div>
</template>

<script>
import axios from 'axios'
axios.defaults.headers["Content-Type"] = "application/x-www-form-urlencoded";
export default ({
   name:"Content04",
  data() 
    {
        return {   
            open:true,
            form: 
            {
                name: '',
                studentNumber:'',
                tel:'',
                room:'',
                credit:'',
                school:'',
            },
        };
    },
    methods:
    { 
         close()
         {
             this.open=true;
             //把表单信息传给后端
            var params = new URLSearchParams()
            params.append('PK_UID',this.$store.state.uid);
            params.append('Uaddress',String(this.form.room));
            params.append('Uintroduction',String(this.form.school));
            params.append('Uname',String(this.form.name));
            params.append('Uphone',String(this.form.tel));

            axios.post('/api/user/updateUserInf',params)
            .then((response)=>{
                console.log(response.data);
                if(response.data.success==true)
                {
                    this.$message({
                        message: '修改成功',
                        type: 'success'
                    });
                }
                else
                {
                    this.$message.error('修改失败');
                }
                          
            })
         }
    },
    mounted()
    {
       //获取根据学号用户信息
       axios.get('/api/getUserInf?number='+this.$store.state.studentNumber)
       .then((response)=>{
              console.log(response.data); //测试   
              this.form.name=response.data.data.user.uname; 
              this.form.studentNumber=this.$store.state.studentNumber;
              this.form.tel=response.data.data.user.uphone;
              this.form.room=response.data.data.user.uaddress;
              this.form.credit=response.data.data.user.ulevel;
              this.form.school=response.data.data.user.uintroduction;
        })
    }
})
</script>


<style scoped>
#Content04
{
    width: 60%;
    position: absolute;
    left:20%;
}
.a
{
    position: absolute;
    margin-top: 40px;
    left: 50%;
    width: 100px;
}
.b
{
    position: absolute;
    margin-top: 40px;
    left:35% ;
    width: 100px;
}
#first
{
    background-color:#e8e5e5;
    width:auto;
    height: 680px;
}
</style>