<template>
<!--导航组件-->
    <div id="Nav">
        <el-menu  :default-active="this.$route.path" router="true" class="el-menu-demo"  mode="horizontal"  
        background-color="#a13413"   text-color="#fff"  active-text-color="#ffd04b">
            <el-menu-item :index="path1"><i class="el-icon-goods"></i>购物中心</el-menu-item>
            <el-menu-item :index="path2"><i class="el-icon-s-order"></i>订单中心</el-menu-item>
            <el-menu-item :index="path3"><i class="el-icon-s-shop"></i>出售商品</el-menu-item>
             <el-submenu>
                <template slot="title"><i class="el-icon-user"></i>个人中心</template>
                <el-menu-item :index="path4"><i class="el-icon-s-custom"></i>个人资料</el-menu-item>
                <el-menu-item ><el-button type="text" @click="back()" class="a"><i class="el-icon-s-promotion"></i>退出登录</el-button>
                </el-menu-item>    
            </el-submenu>
        </el-menu>      
        <router-view></router-view> 
    </div>
</template>


<script>
export default {
    name:"Nav",
    data()
    {
        return{
            path1:'/user/content01',
            path2:'/user/content02',
            path3:'/user/content03',
            path4:'/user/content04',
        }
    },
    methods:
    {
        back()
        {       
            this.$confirm('你是否要推出登录？', '提示', 
            {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$router.push({path:"/"});
                this.$message({
                    type: 'success',
                    message: '退出登录成功!'
                });
                }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消退出登录'
                });          
                });    
        },
    },
}
</script>

