<template>
<!--商品中心-->
<div id="first">
    <div id="Content01" align="center" >
           
            <!--搜索框-->
            <div id="two">
                <el-input  placeholder="请输入你想搜素的商品名称"  v-model="keyword"  clearable class="two1"></el-input>
                <el-button type="danger" icon="el-icon-search" @click="search">搜索</el-button>
            </div>

            <!--走马灯-->
            <div id="one">
                <el-carousel >
                    <el-carousel-item v-for="(item,index) in items" :key="index" class="el-carousel__item">
                            <img class="carousel-image" :src="item.url" height="300px">
                    </el-carousel-item>
                </el-carousel>
            </div>

            <!--商品列表-->
            <div id="three">
                <el-table :data="search"  stripe  style="width: 100%" :default-sort="{ prop:'sprice',order:'descending' }">
                    <el-table-column  prop="sname"    label="商品名称"  width="180"></el-table-column>                              
                    <el-table-column  prop="sphoto"     label="商品图片"  width="180">
                        <template slot-scope= "scope">
                        <img :src="scope.row.sphoto" alt= "加载失败" style= "width:50px;height:50px">
                        </template>
                    </el-table-column>
                    <el-table-column  prop="sprice"    label="价格"  width="180" sortable></el-table-column>  
                    <el-table-column  label="详细信息" >
                        <template slot-scope= "scope">
                        <el-button type="text" @click="find(scope.row)">查看详细信息</el-button>
                        </template>   
                    </el-table-column>
                    <el-table-column   label="操作">
                        <template slot-scope= "scope">
                        <el-button type="danger" round size="small" @click="add(scope.row)">加入购物车</el-button>   
                        </template>               
                    </el-table-column>
                </el-table>
            </div>  

            <el-pagination  layout="prev, pager, next"  :total="goods.length"  :page-size="pageSize" :current-page="currentPage" @current-change="handleCurrentChange" style="text-align: center" >
            </el-pagination>    
    </div>
</div>
</template>

<script>
import axios from 'axios'
axios.defaults.headers["Content-Type"] = "application/x-www-form-urlencoded";
export default ({
name:"Content01",
data()
   {
        return{
            keyword:'',
            items:[
                {url:require('../assets/a.jpg')},
                {url:require('../assets/b.jpg')},
                {url:require('../assets/c.jpg')},
                {url:require('../assets/e.jpg')},         
            ], 
            goods:[],
            currentPage: '1', //当前页 刷新后默认显示第一页
            pageSize: '5', //每一页显示的数据量 此处每页显示5条数据
            
       };
   },
   methods:
   {
        //点击按钮切换页面
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化   
        },
         

       //查看商品详细信息
        find(row) 
        {       
            
            var name1;
            var tel1;
            var credit1;
              
            //通过卖家id查询卖家信息
            //要封装成formdata类型
            var params = new URLSearchParams()
            params.append('PK_UID', row.suid)
            axios.post('/api/user/getOneUser',params)
            .then((response)=>{
                    console.log(response.data); //测试  
                    name1=response.data.data.user.uname;
                    tel1=response.data.data.user.uphone; 
                    credit1=response.data.data.user.ulevel;   
                     //弹出商品的基本信息
                    this.$alert("<div>卖家名字: "+name1+"</div>"+"<div>卖家电话: "+tel1+"</div>"+
                        "<div>卖家信用度: "+credit1+"</div>"+"<div>商品简介: "+row.sintroduction+"</div>"  
                        , '商品信息', {
                    dangerouslyUseHTMLString: true
                    });
            }) 
        },


       //把商品添加到购物车,像后端发消息
        add(row)
        {
             
            //要封装成formdata类型
            var params = new URLSearchParams()
            params.append('O_SID',row.pkSid)
            params.append('O_buyerUID', this.$store.state.uid)
            axios.post('/api/orderlist/add2Cart',params)
            .then((response)=>{
                console.log(response.data); //测试  
                  if(response.data.success==true)
                  {
                        this.$message({
                            message: '已经成功添加到购物车',
                            type: 'success'
                        });
                  }
                  else
                  {
                      this.$message.error('学号或密码错误');
                  }
            }) 
        }

      
   },
   computed:
   {   
        //实现搜索表格的功能
         search()
         {
              var newlist=[];                         
              this.goods.forEach(element => {                            
                  if(element.sname.indexOf(this.keyword)!=-1&&element.state==1)
                  {
                     newlist.push(element);
                  }
              });
              return newlist.slice((this.currentPage - 1) * this.pageSize,this.currentPage *this.pageSize);
         },  
   },
   mounted()
   {
       //获取用户id 
       axios.get('/api/getUserInf?number='+this.$store.state.studentNumber)
       .then((response)=>{
              console.log(response.data); //测试
              this.$store.commit('setUid',response.data.data.user.pkUid); //保存用户id       
        })
  
       //获取已经通过商品列表
       axios.get('/api/user/userGetShopinf')
       .then((response)=>{
              console.log(response.data); //测试
              this.goods=response.data.data.goods; 
        })
  
   }
})
</script>

<style scoped>
#Content01
{
    width: 60%;
    position: absolute;
    left:20%;
}
#one
{
     background-color: #99a9bf ;
     height: 300px;
     margin-top: 50px;
    
}
#two
{
    margin-top: 30px;
}
#three
{
   margin-top: 50px;
}
.el-carousel__item 
{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.carousel-image
{
    max-width: 100%;
    max-height: 100%;
}
.two1
{
    width:90%;
}
#first
{
    background-color:#e8e5e5;
    width:auto;
    height: 1000px;
}
</style>
