<template>
<div id="Register">
       
       <div id="img"><img src="../assets/3d-stripy-shopping-online.png" alt=""></div>
       <div id="two">
            <center><h1>用户注册</h1></center>
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm" id="form2" >
              <el-form-item label="学号" prop="studentNumber">
                    <el-col :span="20">
                    <el-input v-model="ruleForm.studentNumber"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="密码" prop="pass">
                     <el-col :span="20">
                    <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="确认密码" prop="checkPass">
                    <el-col :span="20">
                    <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="姓名" prop="name">
                    <el-col :span="20">
                    <el-input v-model="ruleForm.name"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item label="电话" prop="tel">
                    <el-col :span="20">
                    <el-input v-model="ruleForm.tel"></el-input>
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="submitForm('ruleForm')" class="btu">提交</el-button>
                    <el-button @click="resetForm('ruleForm')" class="btu">重置</el-button>
                </el-form-item>
            </el-form>
        </div>
        
</div>
</template>

<script>
import axios from 'axios'
axios.defaults.headers["Content-Type"] = "application/x-www-form-urlencoded";
export default {
    name:"Register",  
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          if (this.ruleForm.checkPass !== '') {
            this.$refs.ruleForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          studentNumber:'',
          pass: '',
          checkPass: '',
          name:'',
          tel:'',     
        },
        
        rules: {
          studentNumber: [
            { required: true, message: '请输入学号',trigger: 'blur' },
            { min: 9, max: 9, message: '学号长度应为9位数', trigger: 'blur' }
          ],
          pass: [
            { validator: validatePass, trigger: 'blur' },
            { min: 6, max: 10, message: '密码长度应为6到10位数', trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          name: [
            { required: true, message: '请输入名字', trigger: 'blur' }
          ],
          tel: [
            { required: true, message: '请输入电话', trigger: 'blur' },
            { min: 11, max: 11, message: '电话号码长度应为11位', trigger: 'blur' }
          ],
        }
      };
    },
    methods: {

      //把注册表传给后端
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) 
          {
              //要封装成formdata类型
              var params = new URLSearchParams()
              params.append('Uname', String(this.ruleForm.name))
              params.append('Unumber',String(this.ruleForm.studentNumber))
              params.append('Upassword',String(this.ruleForm.pass))
              params.append('Uphone', String(this.ruleForm.tel))
          
              axios({
                headers:{'Content-Type':'application/x-www-form-urlencoded'},
                method: 'post',
                url: '/api/registerUserWithPhone',
                data:params,
              }).then((response)=>{
                      console.log(response.data); //测试
                      if(response.data.success==true)
                      {
                          this.$message({
                                  message: '注册成功',
                                  type: 'success'
                          });  
                          this.$refs[formName].resetFields();
                          this.$router.push({path:"/"});
                      }
                      else
                      {
                           this.$message.error("学号已经被注册");
                      }
              });
          } 
          else 
          {
            console.log('error submit!!');
            return false;
          }
        });
      },

      //重置
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

    }
  }
</script>

<style scoped>
#Register
{
   background-color: #e8e5e5;
   width: 1510px;
   height: 740px;
}
.btu
{
    position: relative;
    left:9%;
    width: 150px;
}
img
{
  width: 40%;
}
#img
{
  position: absolute;
  left: 5%;
  top:20%
}
#two
{
    width: 35%;
    position: absolute;
    left:30%;
    top: 10%;  
}
el-input
{
  border-radius: 20px;
}


</style>