import Vue from "vue"
import Vuex from "vuex"
 
Vue.use(Vuex);
 
export default new Vuex.Store({
    state:{
        uid: "",            //用户id
        studentNumber:'',   //用户学号
    },
    mutations:{
        // 设置用户id
        setUid(state,uid){
            state.uid = uid;
        },
        // 设置用户学号
        setStudentNumber(state,studentNumber){
            state.studentNumber = studentNumber;
        },
    }
})