<template>
<!--登录组件-->
<div id="one">
  <div id="img"></div>
  <div id="Login">
        <center><h1>用户登录</h1></center>
         <el-form ref="form" :model="form" :rules="rules" label-width="60px" class="demo-form-inline">
          <el-form-item  label="学号" prop="studentNumber" id="two">  
            <el-col :span="20">
              <el-input  v-model="form.studentNumber" autocomplete="off" placeholder="请输入学号"></el-input>
            </el-col>
          </el-form-item>
          <el-form-item label="密码" prop="password" id="three">
               <el-col :span="20">
               <el-input type="password" v-model="form.password" autocomplete="off" placeholder="请输入密码"></el-input>
               </el-col>    
          </el-form-item>
          <el-form-item id="four">
             <el-col :span="20">
             <el-button type="danger" @click="submitForm('form')" >登录</el-button>
             <el-button @click="register">注册</el-button>
             </el-col>
          </el-form-item>
        </el-form>
  </div>
</div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Login',
  data()
  {
    return {
        form:
        {
          studentNumber:'',
          password:'',
        },
        rules:
        {
           studentNumber:[{ required: true, message: '请输入学号', trigger: 'blur' }],
           password:[{ required: true, message: '请输入密码', trigger: 'blur' }],
        },
    }
  },
  methods:
    {
        //登录功能
        submitForm(formName)
        {
            this.$refs[formName].validate((valid) => {
              if (valid)
              {
                  //要封装成formdata类型
                  var params = new URLSearchParams()
                  params.append('username', String(this.form.studentNumber))
                  params.append('password',String(this.form.password))

                  axios.post('/api/login',params)
                    .then((response)=>{
                            console.log(response.data);
                            //如果成功切换路由
                            if(response.data.success==true&&response.data.data.auth[0].authority=="ROLE_user")
                            {
                              
                                this.$store.commit('setStudentNumber',this.form.studentNumber); //保存学号

                                  //获取用户id 
                                  axios.get('/api/getUserInf?number='+this.$store.state.studentNumber)
                                  .then((response)=>{
                                          console.log(response.data); //测试
                                          if(response.data.data.user.ustate==0)    
                                          {
                                                this.$message({
                                                    message: '登录成功',
                                                    type: 'success'
                                                });   
                                               this.$router.push({name:"content01"});//跳转
                                          } 
                                          else
                                          {
                                               this.$message.error('你已被列入黑名单，禁止登录');
                                          }
                                  })
                            }
                            else 
                            {
                                this.$message.error('学号或密码错误');
                            }
                  })

              } 

              else  return false;
            });
        },
        //注册功能
        register:function(){

            this.$router.push({path:"/register"});

        }
    }
}
</script>


