import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import UserView from '../views/UserView.vue'
import Content01 from '../components/Content01.vue'
import Content02 from '../components/Content02.vue'
import Content03 from '../components/Content03.vue'
import Content04 from '../components/Content04.vue'
import Register from '../components/Register.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/register',
    name: 'Register',
    component: Register
  },
  {
    path: '/user',
    name: 'user',
    component:UserView,
    children:[
       {
            path:'content01',
            name:'content01',
            component:Content01
       },
       {
            path:'content02',
            name:'content02',
            component:Content02
       },
       {
            path:'content03',
            name:'content03',
            component:Content03
       },
       {
            path:'content04',
            name:'content04',
            component:Content04
       },
    ]    
  }
]

const router = new VueRouter({
  routes
})

export default router
