<template>
<!--出售商品-->
<div id="Content03" >
    <div id="one">
        <center><h1>上传商品</h1></center>
        <div id="two">
            <el-form ref="form" :model="form" :rules="rules" label-width="100px" id="form1">
                <el-form-item label="商品名称" prop="name">
                    <el-input v-model="form.name" placeholder="请输入商品名称" required></el-input>
                </el-form-item>
                <el-form-item label="商品价格"  prop="cost">
                    <el-input v-model.number="form.cost" placeholder="请输入商品价格"></el-input>
                </el-form-item>
                <el-form-item label="商品简介" prop="about">
                    <el-input type="textarea" v-model="form.about" placeholder="请输入商品简介"></el-input>
                </el-form-item>
                <el-form-item label="上传图片" prop="url">        
                    <el-upload
                            
                            ref="upload"
                            list-type="picture-card"
                            action="#"
                            accept=".jpg, .jpeg, .png, .bmp"
                            :before-upload="beforeUpload"   
                            :http-request="httpRequest"
                            :on-change="handleChange"
                            :auto-upload="false"
                            :limit="1"
                            :file-list="fileList">
                            <!--:data="form"-->

                            <i slot="default" class="el-icon-plus"></i>
                            <div slot="file" slot-scope="{file}">
                                <!--展示图片-->
                                <img   class="el-upload-list__item-thumbnail" :src="file.url" alt="" >
                                <span class="el-upload-list__item-actions" >
                                    <span
                                        class="el-upload-list__item-preview"
                                        @click="handlePictureCardPreview(file)"
                                    >
                                        <i class="el-icon-zoom-in"></i>
                                    </span>
                                    <span
                                        v-if="!disabled"
                                        class="el-upload-list__item-delete"
                                        @click="handleRemove(file)"
                                    >
                                    <i class="el-icon-delete"></i>
                                    </span>
                                </span>
                            </div>
                            <template #tip>
                                <div style="font-size: 12px;color: #919191;">
                                只能上传一张图片哦！！！
                                图片只能是.jpg, .jpeg, .png, .bmp格式
                                </div>
                            </template>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
                <el-form-item>
                    <el-button type="danger" @click="submitForm('form')" class="a">上传商品</el-button>
                    <el-button @click="resetForm('form')" class="b">重置</el-button>
                </el-form-item>
            </el-form>  
        </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'
export default ({
   name:"Content03",
    data() 
    {
        return {
            dialogVisible: false,
            disabled: false,
            imageVisible:true,
            dialogImageUrl: '',
            form: 
            {
                name: '',
                cost:'',
                about:'',
                url:null,
                
            },
            fileList:[],
            rules:
            {
               name:[{ required: true, message: '请输入商品名称', trigger: 'blur' }],
               cost:[{ required: true, message: '请输入价格', trigger: 'blur' }],
               about:[{ required: true, message: '请输入商品简介', trigger: 'blur' }],
               url:[{ required: false, message: '请上传商品图片', trigger: 'blur' }],
            },
        };
    },
    methods: 
    {

           
            // param是自带参数, this.$refs.upload.submit() 会自动调用 httpRequest方法.在里面取得file
            httpRequest(param) 
            {
                    console.log(param)
                    let fileObj = param.file // 取得的files
                    let fd = new FormData()// FormData 对象
                    fd.append('UID', this.$store.state.uid)
                    fd.append('file', fileObj)          // 文件对象
                    fd.append('introduction', this.form.about)
                    fd.append('name', this.form.name)
                    fd.append('price', this.form.cost)
                    
                    
                    let config = {
                        headers: {
                        'Content-Type': 'multipart/form-data'
                        }
                    }
                    axios.post('/api/user/sellOneShop', fd, config)
                    .then(res=>{   
                            this.submitForm();//提交表单  
                            console.log(res);          
                    })
            },

            submitForm(formName) {
                let vm = this;
                this.$refs[formName].validate((valid) => {
                    if (valid) 
                    {
                        vm.$refs.upload.submit();      //调用 httpRequest方法
                        vm.$refs[formName].resetFields();
                        vm.$message({
                            message: '上传成功',
                            type: 'success'
                        });
                    } 
                    else 
                    {
                        return false;
                    }
                });
            },

            resetForm(formName) {
                this.$refs[formName].resetFields();
                this.form.url = '';
                this.$refs.upload.clearFiles();
            },

            //放大查看图片
            handlePictureCardPreview(file) {
                this.dialogImageUrl=file.url;
                this.dialogVisible = true;
            },

            handleChange (file) {
                this.form.url = URL.createObjectURL(file.raw);
            },

            handleRemove(file) {
                  this.$refs.upload.clearFiles();
            },
 
            beforeUpload(file) {
                return true;
            },
    },
})
</script>

<style scoped>
#one
{
    width: 40%;
    position: absolute;
    left:30%;
    top: 10%;  
}
#Content03
{
    background-color:#e8e5e5;
    width: auto;
    height: 680px;
}
.a
{
    width: 100px;
    position: absolute;
    left: 26%;
}
.b
{
    width: 100px;
    position: absolute;
    left: 45%;
}
</style>